            this.top5Data1.push(item.codeQuality);
            this.top5Data2.push(item.bugs);
            this.top5Data3.push(item.testBugs);
            this.top5Data4.push(item.ut);
            this.top5Data5.push(item.manHaur);
          });
          this.drawLine();
        })
        .catch(err => {
          console.log(err, "err错误");
        });
    },
    way2(data) {
      this.$http
        .post("http://10.1.41.99:8080/report/progress", data)
        .then(response => {
          console.log(response.data.data, "圆环");
          this.roundData4 = response.data.data.delayDeveloping;
          this.roundData3 = response.data.data.delayReleased;
          this.roundData2 = response.data.data.developing;
          this.roundData1 = response.data.data.released;
          let num =
            this.roundData1 +
            this.roundData2 +
            this.roundData3 +
            this.roundData4;
          this.roundData = (
            (this.roundData1 + this.roundData2) /
            num *
            100
          ).toFixed(0);
          this.color1 = Number((this.roundData1 / num).toFixed(1));
          this.color2 =
            this.color1 + Number((this.roundData2 / num).toFixed(1));
          this.color3 =
            this.color2 + Number((this.roundData3 / num).toFixed(1));
          this.color4 =
            this.color3 + Number((this.roundData4 / num).toFixed(1));
          this.drawRound();
        })
        .catch(err => {
          console.log(err, "err错误");
        });
    }
  },
  mounted() {
    this.$http
      .post("http://10.1.41.99:8080/base/project", {})
      .then(response => {
        console.log(response.data, "222222222");
        response.data.data.map((item, index) => {
          this.optionsBig.push({
            value: item.pid,
            label: item.pname
          });
          this.options.push({
            id: item.pid,
            value: item.subPid,
            label: item.subPname
          });
        });
      })
      .catch(err => {
        console.log(err, "err错误");
      });
    this.way1({});
    this.way2({});
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="scss" scoped="" type="text/css">
$color: white;
$height: 5.066667rem;
.header {
  display: flex;
  padding: 0.266667rem /* 20/75 */ 1.33rem /* 100/75 */;
  justify-content: space-between;
  background: #222222;
  & .el-row {
    width: 100%;
    & .pull-right {
      text-align: right;
    }
    & .header-title {
      color: $color;
      font-size: 0.453333rem /* 34/75 */;
    }
    & div {
      & div {
        display: inline-block;
        position: relative;
        & .header-input {
          width: 2.93rem /* 220/75 */;
          height: 0.53rem /* 40/75 */;
          background: #393939;
          font-size: 0.213333rem /* 16/75 */;
          margin-right: 0.2rem /* 15/75 */;
          border: 1px solid #2a2a2a;
        }
        & i {
          position: absolute;
          right: 20px;
          top: 8px;
          color: $color;
        }
      }
      & .header-search {
        width: 1.57rem /* 118/75 */;
        height: 0.53rem /* 40/75 */;
        font-size: 0.19rem /* 14/75 */;
        line-height: 0.15rem;
        padding: 0;
      }
    }
  }
}
.body {
  text-align: center;
  padding: 0.266667rem /* 20/75 */ 1.33rem /* 100/75 */;
  background: #f2f2f2;
  font-size: 0.24rem /* 18/75 */;
  color: #666666;
  text-align: left;
  & .el-col-3 {
    text-align: right;
  }
  & .body-sure {
    width: 1.52rem /* 114/75 */;
    height: 27px;
    line-height: 3px;
    font-size: 0.213333rem /* 16/75 */;
    background: #fdaa29;
  }
  & .body-selected {
    width: 2.8rem;
    font-size: 0.133333rem /* 10/75 */ !important;
    margin-left: 0.13rem /* 10/75 */;
    & popper-option {
      font-size: 0.133333rem /* 10/75 */;
    }
  }
}
.echarts {
  padding: 0.2rem /* 15/75 */ 1.33rem /* 100/75 */;
  position: relative;
  & #myChart {
    height: $height;
  }
  & #myCharts {
    height: 6.933333rem /* 520/75 */;
  }
  & .btn {
    position: absolute;
    right: 0;
    z-index: 100;
  }
}
.pageNation {
  text-align: center;
  margin-top: 0.133333rem /* 10/75 */;
}
</style>



